import React, { useContext, useState } from 'react';
import { Divider, List } from '@material-ui/core';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import {
    BrowserRouter as Router, Switch, Route, Redirect,
} from "react-router-dom";
//Screens
import Users from "./users";
import Record from "./record";
import Survey from "./survey";
//Custom components
import CustomAppBar from '../components/appbar';
import CustomLink from '../components/listItem';
import CustomDrawer from '../components/drawer';
//Utils
import { withNamespaces } from 'react-i18next';
import { GetStorage, isMobile } from '../utils/functions';
import { COLORS, Keys, Roles } from '../utils/enums';
//Custom styles
import { RowView, NavigationView, ContentView } from '../globalStyles';
import { GlobalContext } from '../contexts/global';

const Header = ({ title }: { title: string }) => {
    return (
        <React.Fragment>
            <Divider />
            <div style={{ backgroundColor: COLORS.PRIMARY_DARK, height: 100 }} >
                <h1 style={{ color: "white", textAlign: "center" }} >{title}</h1>
            </div>
        </React.Fragment>
    )
}

function App({ t }: any) {

    const [drawer, setDrawer] = useState(!isMobile());
    const [actualRoute, setRoute] = useState("");
    
    const globalContext = useContext(GlobalContext);

    const handleDrawer = () => {
        setDrawer(!drawer)
    }

    const closeDrawer = () => setDrawer(false);

    const onClickItem = (name: string) => {
        setRoute(name);
    }

    // alert(globalContext?.user.rol)
    return (
        <Router>
            <React.Fragment >
                <RowView >
                    {globalContext?.user.rol === Roles.ADMIN &&
                        <CustomDrawer open={drawer} closeDrawer={closeDrawer} >
                            <Header title={t("DrawerTitle")} />
                            <Divider />
                            <List>
                                <CustomLink title={t('Survey')} icon="mail" iconColor="white" nameView="/survey" actualRoute={actualRoute} onClick={onClickItem} />
                                {/* Mostrar estos links si el user es admin */}
                                {globalContext?.user.rol === Roles.ADMIN && <>
                                    <CustomLink title={t('Users')} icon="mail" iconColor="white" nameView="/users" actualRoute={actualRoute} onClick={onClickItem} />
                                    <CustomLink title={t('Record')} icon="mail" iconColor="white" nameView="/record" actualRoute={actualRoute} onClick={onClickItem} />
                                </>}
                            </List>
                        </CustomDrawer>}
                    <NavigationView>
                        <CustomAppBar title={window.location.pathname} onMenuPress={handleDrawer} drawerOpened={drawer} />
                        <ContentView>
                            <Switch>
                                <Route path="/survey" exact component={Survey} />
                                {/* Mostrar estos links si el user es admin */}
                                {globalContext?.user.rol === Roles.ADMIN && <>
                                    <Route path="/users" exact component={Users} />
                                    <Route path="/record" exact component={Record} />
                                </>}
                                {globalContext?.user.rol === Roles.USER && <>
                                    <Redirect to="/survey" />
                                </>}
                            </Switch>
                        </ContentView>
                    </NavigationView>
                </RowView>
                <ToastContainer />
            </React.Fragment>
        </Router>
    );
}

export default withNamespaces()(App);
