import React, { useState } from "react";
import { Dialog, DialogTitle, DialogContent, FormControl, DialogActions, Button, ListItemSecondaryActionClassKey, Paper, Grid } from "@material-ui/core";
import { toast } from "react-toastify";
import axios from "axios";
//Custom components
import TextField from "../../components/_textField";
import TextPicker from "../../components/_textSelect";
//Utils
import models from "../../utils/models";
import { withNamespaces } from "react-i18next";
import { GetStorage, SetStorage } from "../../utils/functions";
import { Keys } from "../../utils/enums";
import { answers, questions } from "../../utils/constants";

SetStorage(Keys.User, "ronal2s");

function ModalNew(props: any) {
    // const [form, setForm] = useState({ name: "Miguel", lastname: "Paulino", phone: "809413265" });
    const [form, setForm] = useState({ ...models.questions });
    const t = (props as any).t;

    const handleInputs = (name: string, value: any) => {
        setForm({ ...form, [name]: value });
    }

    const answerSurvey = async () => {
        const serverDate = await axios.get("/api/date");
        
        const obj = {
            user: GetStorage(Keys.User),
            date: serverDate.data,
            questions:
                [
                    //@ts-ignore
                    { question: questions.question1, answer: answers[form.question1] },
                    //@ts-ignore
                    { question: questions.question2, answer: answers[form.question2] },
                    //@ts-ignore
                    { question: questions.question3, answer: answers[form.question3] },
                    //@ts-ignore
                    { question: questions.question4, answer: answers[form.question4] },
                ]
        }
        console.log("Questions: ", obj.questions)
        axios.post("/api/surveys", obj)
            .then(result => toast.success(t("Thanks for your response")))
            .catch(error => toast.error(t("An error has ocurred")))
    }


    return (
        <React.Fragment>
            <Grid container justify="flex-start" >
                <Grid item sm={6} >
                    <Paper style={{ padding: 10 }} >
                        <TextPicker label={t("Question1")} name="question1" list={[t("Yes"), t("No")]} value={form.question1} onChange={handleInputs} /> <br />
                        <TextPicker label={t("Question2")} name="question2" list={[t("Yes"), t("No")]} value={form.question2} onChange={handleInputs} /> <br />
                        <TextPicker label={t("Question3")} name="question3" list={[t("Yes"), t("No")]} value={form.question3} onChange={handleInputs} /> <br />
                        <TextField label={t("Question4")} fullwidth name="question4" value={form.question4} onChange={handleInputs} />


                        {/* <TextField label={t("Fullname")} name="fullname" value={form.fullname} onChange={handleInputs} />
                        <TextField label={t("User")} name="user" value={form.user} onChange={handleInputs} />
                        <TextField label={t("Password")} name="password" value={form.password} password onChange={handleInputs} /> */}
                        {/* <FormControl>
                        </FormControl> */}
                        <DialogActions>

                            <Button variant="contained" color="primary" onClick={answerSurvey} >
                                {t("Accept")}
                            </Button>
                        </DialogActions>
                    </Paper>
                </Grid>
            </Grid>
        </React.Fragment>
    )
}

export default withNamespaces()(ModalNew);