import styled from 'styled-components';
import { Drawer_size } from '../utils/constants';


const RowView = styled.div({
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap"
})

const DrawerView = styled.div({
    width: Drawer_size
})

const NavigationView = styled.div({
    flex: 1,
})

const ContentView = styled.div({
    padding: 10
})
const View = styled.div`
display: ${(props: {
    centered?: boolean, fullsize?: boolean, color?: string, column?: boolean, height?: number, width?: number, borderRadius?: number, opacity?: number,
    row?: boolean, wraps?: boolean, justifyContent?: "center" | "space-around" | "space-between" | "space-evenly", button?: boolean,
    marginLeft?: number, marginRight?: number, alignItems?: "center" | "flex-end" | "flex-start"
}) => props.centered || props.column || props.row ? "flex" : ""};
justify-content: ${props => props.centered ? "center" : props.justifyContent ? props.justifyContent : ""}; 
align-items: ${props => props.centered ? "center" : props.alignItems ? props.alignItems : undefined}; 
background-color: ${props => props.color ? props.color : "white"};
height: ${props => props.fullsize ? "100vh" : props.height ? `${props.height}px` : ""}; 
width: ${props => props.fullsize ? "100%" : props.width ? `${props.width}px` : ""}; 
flex-direction: ${props => props.column ? "column" : props.row ? "row" : "column"};
border-radius: ${props => props.borderRadius ? `${props.borderRadius}px` : ""};
opacity: ${props => props.opacity ? props.opacity : 1};
margin-left: ${props => props.marginLeft ? `${props.marginLeft}px` : 0};
margin-right: ${props => props.marginRight ? `${props.marginRight}px` : 0};
cursor: ${props => props.button ? "pointer" : ""};
flex-wrap: ${props => props.wraps ? "wrap" : undefined}
`

const Title = styled.h1`
    text-align: ${(props: { centered?: boolean, color?: string, margin?: number, size?: number, bold?: boolean, }) => props.centered ? "center" : "left"};
    font-weight: ${props => props.bold ? 'bold' : 100};
    color:${props => props.color ? props.color : "black"};
    margin: ${props => props.margin ? `${props.margin}px` : 0};
    font-size: ${props => props.size ? `${props.size}px` : undefined};
`


const SquareView = styled.div`
    width: 300px; height: 250px;
    background-color: white; 
    border-radius: 10px; 
    box-shadow: 2px 3px 16px 3px;
`

const Separator = styled.div`
    margin-top: ${(props: { size?: number }) => props.size ? `${props.size}px` : "10px"}
`

const BreakLine = styled.br``

export {
    RowView,
    DrawerView,
    NavigationView,
    ContentView,
    SquareView,
    Separator,
    BreakLine,
    Title,
    View
}